<template>
    <div>
        <van-nav-bar
        :title=title
        left-text="返回" 
        @click-left="toback"
        :fixed="true"
        />
        <div class="topdetail-list">
            <div class="list-one clearfix" v-for="(item,index) in books" :key="index" @click="goBooktedail(item._id)">
                <div class="one-img fl">
                    <img :src="item.cover" class="auto-img" alt="">
                </div>
                <div class="fl one-right">
                    <div class="one-title">
                        {{item.title}}
                    </div>
                    <div class="one-detail">
                        {{item.shortIntro}}
                    </div>
                    <div class="one-last">
                        <div class="fl ">作者:{{item.author}}</div>
                        <div class="fr">{{item.majorCate}}</div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                _id:"",
                books:[],
                title:""
            }
        },
        created(){
            this._id = this.$route.query._id;
            this.title = this.$route.query.title;
            
            this.gettopDetail(this._id)
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            imgKey(value) {
                return unescape(value).slice(7);
            },
            gettopDetail(id){
                    this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/rank/"+id,
                        
                    }).then(result => {
                        this.$toast.clear(); 
                      
                        for(let i = 0 ; i < result.data.ranking.books.length ; i++){
                            result.data.ranking.books[i].cover = this.imgKey(result.data.ranking.books[i].cover)
                        }
                        this.books = result.data.ranking.books

                        
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            goBooktedail(id){
                this.$router.push({name:"Bookdetail",query:{id}})
            }
        }
    }
</script>

<style lang="less" scoped>
.topdetail-list{
    margin-top: 46px;
    .list-one{
        border-bottom: 1px solid #ccc;
        padding: 15px  ;
    }
    .one-img{
        width: 85px;
    }
    .one-right{
        width: calc(~"100% - 85px");
    }
    .one-title{
        font-size: 16px;
        font-weight: 700;
        padding: 5px;
    }
    .one-detail{
        padding: 5px 10px;
        color: #ccc;
        height: 46px;
        overflow: hidden;
        text-overflow:ellipsis;
	    white-space:nowrap
    }
    .one-last{
        color: #feb67e;
        padding: 10px;
    }
}
</style>